import React from 'react';


const ExpPagingComponent = () => {
  return (
      <div className="express_paging">
        <span className="pg_prev"></span>
        <span className="pg_ball on"></span>
        <span className="pg_ball"></span>
        <span className="pg_ball"></span>
        <span className="pg_ball"></span>
        <span className="pg_next"></span>
    </div>
  );
};

export default ExpPagingComponent;
